




import Vue from 'vue';
import {Component} from 'vue-property-decorator';
import {mapActions, mapGetters} from 'vuex';

@Component({
  computed: {
    ...mapGetters([
      'username',
      'slug'
    ])
  },
  methods: {
    ...mapActions([
      'checkLogin'
    ])
  }
})
export default class Avatar extends Vue {
  username!: string;
  slug!: string;
  checkLogin!: Function;

  created() {
    this.checkLogin().then();
  }
}
